// src/pages/Login.jsx
import React, { useState } from "react";
import "./Sati.css"; 
import { GoogleLogin } from "@react-oauth/google";

export default function Login({ onClose = () => {} }) {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [otp, setOtp] = useState("");
  const [step, setStep] = useState(1); // 1 = email/pass, 2 = otp
  const [loading, setLoading] = useState(false);
  const [msg, setMsg] = useState("");

  // ---- Login with Email/Password ----
  const handleLogin = async () => {
    setMsg("");
    if (!email || !password) {
      setMsg("Email aur Password required hai");
      return;
    }
    try {
      setLoading(true);
      const res = await fetch("/api/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email, password }),
      });
      const data = await res.json();
      if (!res.ok) {
        setMsg(data.error || "Invalid Email/Password");
        return;
      }
      console.log("Demo OTP:", data.otpDemo);
      setMsg("OTP sent. Please check console (demo).");
      setStep(2);
    } catch (e) {
      setMsg("Network error: " + e.message);
    } finally {
      setLoading(false);
    }
  };

  // ---- Verify OTP ----
  const handleOtpVerify = async () => {
    setMsg("");
    if (!otp) {
      setMsg("Please enter OTP");
      return;
    }
    try {
      setLoading(true);
      const res = await fetch("/api/verify-otp", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email, otp }),
      });
      const data = await res.json();
      if (!res.ok) {
        setMsg(data.error || "Invalid OTP");
        return;
      }
      setMsg("Login successful ✅");
      setTimeout(() => onClose(), 1200);
    } catch (e) {
      setMsg("Network error: " + e.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="login-overlay" onClick={onClose}>
      <div className="login-modal" onClick={(e) => e.stopPropagation()}>
        {/* Top bar with close */}
        <div className="login-topbar">
          <h3>Sign in to NeoLearn</h3>
          <button className="close-btn" aria-label="Close" onClick={onClose}>
            ×
          </button>
        </div>

        {/* Messages */}
        {msg && <div className="login-msg">{msg}</div>}

        {step === 1 && (
          <div className="login-body">
            {/* Email */}
            <label>Email</label>
            <input
              type="email"
              placeholder="e.g. test@gmail.com"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />

            {/* Password */}
            <label style={{ marginTop: 12 }}>Password</label>
            <input
              type="password"
              placeholder="e.g. 12345"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />

            {/* Forgot Password */}
            <p>
  <a href="/forgot-password">Forgot Password?</a>
</p>

            {/* Next/Login Button */}
            <button
              className="btn primary"
              onClick={handleLogin}
              disabled={loading}
              type="button"
            >
              {loading ? "Please wait..." : "Next"}
            </button>

            {/* OR Divider */}
            <div className="or">or</div>

            {/* Google Login */}
            <GoogleLogin
              onSuccess={(credentialResponse) => {
                console.log("Google Login Success:", credentialResponse);

                fetch("/api/google-login", {
                  method: "POST",
                  headers: { "Content-Type": "application/json" },
                  body: JSON.stringify({ token: credentialResponse.credential }),
                })
                  .then((res) => res.json())
                  .then((data) => {
                    console.log("Backend response:", data);
                    if (data.success) {
                      setMsg("Google Login Successful ✅");
                      onClose();
                    } else {
                      setMsg("Google login failed ❌");
                    }
                  });
              }}
              onError={() => {
                console.log("Google Login Failed");
                setMsg("Google Login Failed ❌");
              }}
            />

            {/* Footer */}
            <div className="login-footer">
              <span>
                Don't have an account? <a href="/register">Create new</a>
              </span>
            </div>
          </div>
        )}

        {step === 2 && (
          <div className="login-body">
            <label>Enter OTP</label>
            <input
              type="text"
              placeholder="6-digit OTP"
              value={otp}
              onChange={(e) => setOtp(e.target.value)}
            />

            <button
              className="btn primary"
              onClick={handleOtpVerify}
              disabled={loading}
              type="button"
            >
              {loading ? "Verifying..." : "Verify OTP"}
            </button>

            <div className="login-footer">
              <button className="link" type="button" onClick={() => setStep(1)}>
                ← Back
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}