// src/components/Navbar.jsx
import React, { useState } from "react";

export default function Navbar() {
  const [open, setOpen] = useState(false);

  const links = [
    { label: "Home", href: "/" },
    { label: "Courses", href: "/#courses" },
    { label: "Features", href: "/#features" },
    { label: "Language", href: "/#language" },
    { label: "Contact Us / Help", href: "/#contact" },
    { label: "About Us", href: "/#about" },
  ];

  return (
    <header
      style={{
        background: "#2f7d32",
        color: "#fff",
        position: "sticky",
        top: 0,
        zIndex: 50,
        width: "100%",
      }}
    >
      <div
        style={{
          maxWidth: 1120,
          margin: "0 auto",
          padding: "0 16px",
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
          height: 56,
        }}
      >
        {/* Brand Name */}
        <a
          href="/"
          style={{
            fontWeight: 700,
            fontSize: 20,
            color: "#fff",
            textDecoration: "none",
          }}
        >
          NeoLearn
        </a>

        {/* Desktop Menu */}
        <nav
          className="nav-links"
          style={{
            display: "flex",
            gap: 18,
            alignItems: "center",
          }}
        >
          {links.map((l) => (
            <a
              key={l.label}
              href={l.href}
              style={{
                color: "#fff",
                textDecoration: "none",
                fontWeight: 500,
              }}
            >
              {l.label}
            </a>
          ))}

          {/* Sign In Button */}
          <a
            href="/auth"
            style={{
              marginLeft: 18,
              background: "#fff",
              color: "#2f7d32",
              border: "1px solid #e6e6e6",
              padding: "6px 12px",
              borderRadius: 10,
              fontWeight: 600,
              textDecoration: "none",
            }}
          >
            Sign In
          </a>
        </nav>

        {/* Hamburger Button (Mobile) */}
        <button
          onClick={() => setOpen(!open)}
          style={{
            background: "transparent",
            border: "none",
            color: "#fff",
            fontSize: 26,
            cursor: "pointer",
            display: "none", // abhi default me hide hai, CSS me media query dalenge
          }}
          className="hamburger"
        >
          ☰
        </button>
      </div>

      {/* Mobile Dropdown */}
      {open && (
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            gap: 12,
            background: "#2f7d32",
            padding: "12px 16px",
          }}
        >
          {links.map((l) => (
            <a
              key={l.label}
              href={l.href}
              onClick={() => setOpen(false)}
              style={{ color: "#fff", textDecoration: "none" }}
            >
              {l.label}
            </a>
          ))}

          <a
            href="/auth"
            onClick={() => setOpen(false)}
            style={{
              background: "#fff",
              color: "#2f7d32",
              padding: "8px 14px",
              borderRadius: 10,
              fontWeight: 600,
              textDecoration: "none",
              textAlign: "center",
            }}
          >
            Sign In
          </a>
        </div>
      )}
    </header>
  );
}